const mixin = {
  data () {
    return {
      anouncements: [],
      private: false
    }
  },
  mounted () {
    this.getAnouncements()
  },
  methods: {
    getAnouncements () {
      this.$http.get('announcements').then((response) => {
        this.anouncements = response.data.data

        // var anouncements = JSON.parse(window.localStorage.getItem('anouncements') || '[]')

        this.anouncements.forEach((anouncement, i) => {
          setTimeout(() => {
            // if (!anouncements.find(anounce => anounce._id === anouncement._id)) {
            this.$refs.showAnouncement.showModal(anouncement)
            // anouncements.push(anouncement)
            // window.localStorage.setItem('anouncements', JSON.stringify(anouncements))
            // }
          }, i * 10000)
        })
      })
    }
  }
}

export default mixin
