<template>
  <div class="img-box adv-img adv-img-half-content" data-anima="fade-left" data-trigger="hover" ref="popup"
       v-show="false">
    <a :href="announcement_url" class="img-box anima-scale-up anima lightbox" data-lightbox-anima="fade-top">
    </a>
  </div>
</template>

<script>
export default {
  name: 'GPopUpImage',
  computed: {
    announcement_url: function () {
      return process.env.VUE_APP_ROOT + '/announcements/' + this.item._id
    }
  },
  data () {
    return {
      item: {}
    }
  },
  methods: {
    showModal (item) {
      this.item = item
      this.setUp()
    },
    setUp () {
      window.$(document).ready(function () {
        window.$('.lightbox').each(function () {
          window.$(this).attr('href', window.$(this).attr('data-href'))
          window.$(this).initMagnificPopup()
          window.$(this).click()
        })
      })
    }
  }
}
</script>

