const mixin = {
  methods: {
    getItems () {

    },
    changeLanguage (lang) {
      if (this.$i18n) {
        this.$i18n.locale = lang
      }
      this.getItems()
    }
  },
  mounted () {
    let defaultLocale = window.localStorage.getItem('language')

    if (defaultLocale) {
      this.$i18n.locale = JSON.parse(defaultLocale).locale
    }

    this.$eventHub.$emit('lightbox')
    this.$eventHub.$emit('flex')

    this.getItems()
  },
  created () {
    this.$eventHub.$on('lang-change', this.changeLanguage)

    this.$eventHub.$on('flex', () => {
      window.$('.flexslider').each(function () {
        window.$(this).initFlexSlider()
      })
    })
    this.$eventHub.$on('paralax', () => {
      window.$('.parallax-window').parallax()
    })
    this.$eventHub.$on('lightbox', () => {
      window.$('.lightbox').each(function () {
        window.$(this).attr('href', window.$(this).attr('data-href'))
        window.$(this).initMagnificPopup()
      })
    })
  },
  beforeDestroy () {
    this.$eventHub.$off('lang-change', this.changeLanguage)
    // this.$eventHub.$off('lightbox')
    // this.$eventHub.$off('flex')
  }
}

export default mixin
